
import { Component, ViewChild} from '@angular/core';
import {AlertController,LoadingController, ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
import { ImageProvider } from '../../../providers/image/image';
import { Camera } from '@ionic-native/camera';



@IonicPage()
@Component({
  selector: 'page-asignacioncalusac3-create',
  templateUrl: 'asignacioncalusac3-create.html',
})
export class Asignacioncalusac3CreatePage {
  @ViewChild('fileInput2') fileInput2

  public form                  : FormGroup;
  username: string = "";
  sexos = [ 'Masculino','Femenino'];
  nacademicos = [ 'Primaria','Secundaria','Diversificado','Titulo Universitario'];

  tiposus: any=[];

  public idhorario2		  :any=[];
  public iddia2		  :any=[];
  public idprofesor2		  :any=[];

  public sinivel		  :any='0';
  public image5             	: any='./assets/img/photo256.png';
  public imagen5		     : any='./assets/img/photo256.png';
  idempresa:any;
  public noorden		     : any;
  public identificador		     : any;
  public tipopago		     : any;

  public horario		     : any;
  _ID:any;

  public profesor		     : any;




  public periodo		     : any;
  items: any=[];
  dataxx: any=[];
  resultado: any=[];



  public vhorario  :any=[];
  public vdia  :any=[];
  public vprofesor  :any=[];

  titulo2:any;
  public vgrupo3  :any=[];
  //private _ID                  : String;
  public pageTitle           	: string;


  isDisablednit :any;
  constructor(  private _IMAGE       : ImageProvider, public alertCtrl: AlertController,private loadingCtrl: LoadingController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController,
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
    });
     this.form = this._FB.group({



       imagen5 : ['', Validators.required],

       noorden : ['', Validators.required],

       identificador : ['', Validators.required]


     });

     this.titulo2='una unidad académica'



  }



  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
    this.storage.get('tiposus').then((value) => {
      this.tiposus = value.split(',');



  });
}

  ionViewDidLoad() : void
  {




    this.authService.getReg2b(this.navParams.data.datax._id+'/' +'1/todosautorizaxid','asignacalusacs').then((data) => {
      var  aa:any=[];
      aa=data
      this.dataxx=aa.tt;

     }, (err) => {

       this.msj.showError3('Error',err.error);
     });




    if(this.navParams.data.op=='Estudiante')
    {



    }

    this.titulo2='Asignación '+this.navParams.data.op

  }


  manageGallery2(loading) : void
  {
    let creat='Actualiza';


    if(this.username=='')
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');
      loading.dismiss();
      return;
    }
    if(!this.username)
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');
      loading.dismiss();
      return;
    }

    if(this.image5==undefined  || this.image5=="./assets/img/photo256.png" || this.image5=='')    {     this.msj.showError3('Mensaje','Adjunte imagen de constancia de orden de pago valida');  loading.dismiss();   return;  }




    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }

     let   options       : any	     = {
     operacion:'asignaorden',
     identificador:this.identificador,
     foto5: this.imagen5,

     noorden: this.noorden,

     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' asigna calusac '}
    };



      loading.dismiss();
     if(options){
        this.authService.createReg( this.navParams.data.datax._id,options,'asignacalusacs').then((result) => {
           if (!this.form.valid) { return; }

           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }



  }
  manageGallery() : void
  {



    if(this.username=='')
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');

      return;
    }
    if(!this.username)
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');

      return;
    }







    let alert = this.alertCtrl.create({ title:this.items.nombre + ', está seguro de realizar asignación calusac : ?',
    buttons: [
      {text: 'Si',
        handler: () => {

    const loading = this.loadingCtrl.create({
      spinner: 'bubbles',
      content: "Espere un momento,  Realizando  Operación ......"
    });
    loading.present();

    this.manageGallery2( loading)


    //  // t






        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();

  }


  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }






  opimg: any;

  updateProfileImage(op) {
    this.opimg=op;
    let t1,t2;
    this.translateService.get('SELECCIONE_IMAGEN').subscribe((value) => {
      t1=value;
    });
    this.translateService.get('TOME_IMAGEN').subscribe((value) => {
      t2=value;
    });
    let actionSheet = this.actionSheetCtrl.create({
      title: t1,
      buttons: [//1930
        {
          text: t2,
          handler: () => {
            this.takePhotograph(op);
          }
        },
        {
          text: t1,
          handler: () => {
            this.selectImage(op);
          }
        }
      ]
    });
    actionSheet.present();
  }

  takePhotograph(op) : void
  {
    this.opimg=op;
   if (Camera['installed']()) {
     this._IMAGE
     .takePhotograph()
     .then((image)=>
     {

      switch (op) {

        case '5':
        this.imagen5   	= image.toString();
        this.image5   		= image.toString();
        break

        default:
    }


     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{

    switch (op) {

      case '5':
          this.fileInput2.nativeElement.click();
      break

      default:

    }

   }
  }


  selectImage(op) : void
  { this.opimg=op;
   if (Camera['installed']()) {
     this._IMAGE
     .selectPhotograph()
     .then((image)=>
     {
      switch (op) {

        case '5':
        this.imagen5   	= image.toString();
        this.image5   		= image.toString();
        break
        default:
      }
     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{


    switch (op) {

      case '5':
        this.fileInput2.nativeElement.click();
      break
      default:


   }       }
  }

  public processWebImage(event): void {
    if(event.target.files && event.target.files[0]){
          const reader: FileReader = new FileReader();
          reader.onload = (readerEvent: any) => {

            switch (this.opimg) {

              case '5':
                  this.image5 = readerEvent.target.result;
                  this._IMAGE.generateFromImage(this.image5, 800, 800, 1, data => {
                    this.image5 = data;
                    this.imagen5 =  data;
                });
              break

              default:
              }




   };
      reader.readAsDataURL(event.target.files[0]);
    }
}









}



